.disabledContainer {
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 3;
}

.disabledContainer div {
  text-shadow: 1px 1px 2px black, 0 0 1em red;
  color: white;
  font-weight: bold;
  font-size: 20px;
}
